* {
  font-family: 'Courier New', Courier, monospace;
  scroll-behavior:smooth;

}

html {
  background-color: #0f0f0f;
}

#intro {
  background-color: #0f0f0f;
  color: #aeaeae;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: left;
  justify-content: center;
  flex-direction: column;
  font-size: 60px;
  font-weight: bold;
  font-family: 'Courier New', Courier, monospace;
  padding-left: 10%;
  box-sizing: border-box;
}

#intro div{
  /* background-color: red; */
  margin-bottom: 10px;
}

#hello {
  font-size: 30px;
}

#name {
  color: #aeaeae;
}

#role {
  color: #593700;
}

#inform-to-scroll-down {
    position: absolute;
    font-size: 1rem;
    bottom: 50px;
    text-align: center;
    width: 100%;
    left: 0;
}

#header{
  position: fixed;
  top: 0px;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  height: 4rem;
  background: #0f0f0f;
  z-index: 10;
}

#right-section, #left-section {
  display: flex;
}

#right-section {
  margin-right: 10px;
}

#right-section a, #logo {
  background: none;
  outline: none;
  border: none;
  color: #aeaeae;
  padding: 10px;
  margin: 10px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
}

#logo {
  font-size: 25px;
  font-weight: bold;
}

#right-section a:hover {
  color: #593700;
  font-weight: bold;
  /* transform: scale(1.05, 1.05); */
  /* background: #1a1a1a; */
}


/* Experience and Skill Common Part */
/* Start */

#experience {
  width: 100vw;
  min-height: 100vh;
  padding-bottom: 30px;
}

#heading, #heading-closed{
  padding-top: 5rem;
  color: #593700;
  font-weight: bold;
  font-size: 23px;
  text-align: left;
  width: 95vw;
  margin: auto;
}

#heading-closed{
  padding-top: 30px;
}

#experience #heading {
  padding-bottom: 30px;
}

#experience-tiles {
  width: 95vw;
  display: grid;
  grid-template-columns: 50% 4px 50%;
  margin: auto;
  
} 

.path {
  background: #593700;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.experience-content {
  margin: 20px;
  color: #aeaeae;
  background: #0f0f0f;
  border-radius: 10px;
  padding: 20px;
  /* min-height: 100px; */
  /* background: #0f0f0f;
  box-shadow:  5px 5px 10px #0a0a0a,
           -5px -5px 10px #141414;
           background: #0f0f0f;
box-shadow: inset 5px 5px 10px #0a0a0a,
            inset -5px -5px 10px #141414 */
}

.duration {
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: left;
  padding: 10%;
  color: #593700;
  border-radius: 0px;

}

.duration-left {
  justify-content: right;
}

.circle {
  position: absolute;
  width: 25px;
  height: 25px;
  background: #593700;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inner-circle {
  width: 18px;
  height: 18px;
  background: #161616;
  border-radius: 100%;
}

.company {
  /* color: #0043ce; */
  font-size: 20px;
  padding: 10px
}
.duration-dates {
  font-size: 13px;
}

.project-name {
  background: none;
  padding-bottom: 10px;
  font-weight: bold;
}

hr {
  border-color: #aeaeae;
  opacity: 0.5;
}

.role {
  font-size: 13px;
  background: none;
}

ul {
  list-style-type: circle;
  font-size: 14px;
  outline: none;
  padding: 10px 20px;
}

ul li {
  padding: 5px;
  line-height: 25px;
}
/* END */

/* CompaniesWorkedSoFar */
/* Start */

.company-logo-in-experience-container{
  text-align: center;
  position: sticky;
  top: 4rem;
  z-index: 5;
}

.company-logo, .company-logo-in-experience {
  width: 60px;
  height: 60px;
  background: #593700;
  padding: 4px;
}

.company-logo-in-experience{
  mix-blend-mode: screen;
  /* box-shadow: inset 5px 5px 10px #0a0a0a,
  inset -5px -5px 10px #141414  */
}

.company-logo img {
  height: inherit;
  width: inherit;
  mix-blend-mode: screen; 
}

#companies {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

#companies-worked-so-far #heading {
  font-size: 13px;
}

/* END */

/* Skills */
/* Start */

#skills-list {
  width: 95vw;
  display: flex;
  justify-content: center;
  margin: auto;
  margin-top: 30px;
  gap: 20px;
  flex-wrap: wrap;
}

.skill-container {
    width: 80px;
    height: 80px;
    background: #161616;
    padding: 30px;
    border-radius: 10px;
    color: #aeaeae !important;
    display: flex;
    justify-content: center;
    align-items: center;
    /* font-weight: bold; */
    font-size: 0.8rem;
    flex-direction: column;
    gap: 10px;  
    background: #0f0f0f;
    box-shadow:  5px 5px 10px #0a0a0a,
             -5px -5px 10px #141414;
}

#skills {
  background: #0f0f0f;
  padding-bottom: 30px;
}

.skill-logo {
  width: 70px;
  height: 70px;
  opacity: 0.6;
}

.skill-logo:hover {
  opacity: 1;
}

/* END */


/* Contact */
/* Start */

#contact {
  padding: 50px;
}

#contact-list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.contact-logo img {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.contact-logo a {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #aeaeae;
  text-decoration: none;
  font-size: 0.8rem;
}


/* END */

/* Certications */
/* Start */

#certifications {
  padding-bottom: 30px;
}

#certificate-list {
  display: flex;
  flex-wrap: wrap;
  width: 95vw;
  margin: auto;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 30px;
}

.certification {
  border-radius: 10px;
  width: 300px;
  height: 150px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background: #0f0f0f;
  /* box-shadow: inset 5px 5px 10px #0a0a0a,
          inset -5px -5px 10px #141414; */
  box-shadow:  5px 5px 10px #0a0a0a,
  -5px -5px 10px #141414;
  color: #aeaeae;
  position: relative;
  text-align: center;
}

.certification .title {
  font-size: 15px;
}

.issued-by {
  position: absolute;
  bottom: 0px;
  right: 0px;
  padding: 20px 10px;
  font-size: 12px;
  color: #593700;
}

.certification .link, .certification .link img {
  width: 20px;
  height: 20px;
}

.certification .link {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
  margin: 10px;
  box-shadow: inset 2px 2px 3px #0a0a0a,
        inset -2px -2px 3px #141414;
}

/* End */

.company-low-res {
  font-size: 13px !important;
  line-height: 30px;
  display: none;
}

.duration-low-res {
  font-size: 13px;
  line-height: 30px;
  display: none;
  text-align: right;
}


/* Footer */
/* Start */

#footer {
  font-size: 12px;
  text-align: center;
  color: #aeaeae;
}

#footer #text {
  display: flex;
  align-items: center;
  justify-content: center;
}

#footer strong {
  padding-left: 10px;
}

#footer img {
  width: 20px;
  height: 20px;
  padding: 5px;
  opacity: 0.6;
}

/* End */

@media only screen and (max-width: 1000px) {
  #intro *{ 
    font-size: 0.8em !important;
  }
  #hello {
    font-size: 0.4em !important;
  }
  #header #left-section a{
    font-size: 18px;
  }
  #header #right-section a{
    font-size: 13px;
  }
  #right-section a:not(:last-child) {
    display: none;
  }
  
  #heading, #heading-closed {
    font-size: 17px !important;
    padding-top: 2rem !important;
  }

  .skill-container {
    font-size: 0.7rem;
    width: 50px;
    height: 50px;
    padding: 40px;
  }


  .company-logo-in-experience-container {
    text-align: left;
    padding-left: 2rem;
    display: none;
  }
  .company-logo, .company-logo-in-experience {
    width: 50px;
    height: 50px;
    padding: 0px;
  }

  #experience-tiles {
    grid-template-columns: 100%;
  }

  #experience-tiles > div.path, #experience-tiles > div.duration {
    display: none;
  }

  #experience-tiles ul {
    font-size: 12px;
  }

  #experience-tiles .project-name {
    font-size: 13px;
    line-height: 20px;
  }

  .company-low-res, .duration-low-res {
    display: block;
  }

  .contact-logo a div {
    display: none;
  }

  .experience-content {
    background: #0f0f0f;
    box-shadow:  5px 5px 10px #0a0a0a,
             -5px -5px 10px #141414;
  }

  #heading-closed {
    display: none;
  }

  #heading {
    text-align: center !important;
  }

}

